html, body {
    box-sizing: border-box;
    min-height: 100%;
}
 body {
    box-sizing: border-box;
    font-size: 1.5em;
    line-height: 1.6;
    font-weight: 400;
    font-family: "Lato", Helvetica, Arial, sans-serif;
    color: #222;
 }
.navbar {
    height: 10vh;
    min-height: 70px;
}
footer {
    box-sizing: border-box;
    height: 8.5vh;
    min-height: 40px;
}
main {
    align-items: stretch;
    background: radial-gradient(#485563, #29323c);
    border-bottom: 4px solid #252525;
    border-top: 3px solid #333;
    box-sizing: border-box;
    color: #fff;
    display: flex;
    height: 82vh;
    padding: 67px 0 27px 0;
    text-shadow: 0px 1px 3px #333;
    text-align: center;
}
.contain {
    padding: 0 50px;
    position: relative;
}


 @media (min-width: 550px) {
    .container {
        width: 80%;
    }
}


.container{position:relative;width:100%;max-width:1400px;margin:0 auto;padding:0 20px;box-sizing:border-box}
.column,.columns{width:100%;float:left;box-sizing:border-box}
@media (min-width: 400px){.container{width:85%;padding:0}}
@media (min-width: 550px){
    .container{width:80%}
    .column,.columns{margin-left:4%}
    .column:first-child,.columns:first-child{margin-left:0}
    .one.column,.one.columns{width:4.6666666667%}
    .two.columns{width:13.3333333333%}
    .three.columns{width:22%}
    .four.columns{width:30.6666666667%}
    .five.columns{width:39.3333333333%}
    .six.columns{width:48%}
    .seven.columns{width:56.6666666667%}
    .eight.columns{width:65.3333333333%}
    .nine.columns{width:74%}
    .ten.columns{width:82.6666666667%}
    .eleven.columns{width:91.3333333333%}
    .twelve.columns{width:100%;margin-left:0}
    .one-third.column{width:30.6666666667%}
    .two-thirds.column{width:65.3333333333%}
    .one-half.column{width:48%}
    .offset-by-one.column,.offset-by-one.columns{margin-left:8.6666666667%}
    .offset-by-two.column,.offset-by-two.columns{margin-left:17.3333333333%}
    .offset-by-three.column,.offset-by-three.columns{margin-left:26%}
    .offset-by-four.column,.offset-by-four.columns{margin-left:34.6666666667%}
    .offset-by-five.column,.offset-by-five.columns{margin-left:43.3333333333%}
    .offset-by-six.column,.offset-by-six.columns{margin-left:52%}
    .offset-by-seven.column,.offset-by-seven.columns{margin-left:60.6666666667%}
    .offset-by-eight.column,.offset-by-eight.columns{margin-left:69.3333333333%}
    .offset-by-nine.column,.offset-by-nine.columns{margin-left:78%}
    .offset-by-ten.column,.offset-by-ten.columns{margin-left:86.6666666667%}
    .offset-by-eleven.column,.offset-by-eleven.columns{margin-left:95.3333333333%}
    .offset-by-one-third.column,.offset-by-one-third.columns{margin-left:34.6666666667%}
    .offset-by-two-thirds.column,.offset-by-two-thirds.columns{margin-left:69.3333333333%}
    .offset-by-one-half.column,.offset-by-one-half.column{margin-left:52%}
}
